<template>
    <div class="hello">
        <p> Lorem ipsum dolor sit amet, consectetur adipisicing elit. Alias animi, at aut debitis dolor doloribus excepturi fugit id, nesciunt obcaecati, praesentium quos voluptatem. Adipisci nemo porro praesentium saepe velit? Asperiores at deserunt ducimus eius eligendi eos laudantium maiores, maxime minima neque quas ratione recusandae saepe sed tempora, tempore, velit veritatis. Ab adipisci amet animi architecto atque autem consequatur cum dignissimos dolor dolores earum, enim ex exercitationem fuga harum illo ipsum itaque iure, laudantium maxime modi nesciunt nulla obcaecati odio porro quaerat quam qui quis, quo rem repellendus sed sit suscipit veritatis vero vitae voluptate? Amet commodi consequatur ea eveniet iusto. </p>

        <transition name="modal">
            <div class="modal-mask">
                <div class="modal-wrapper">
                    <div class="modal-container">
                        <button class="button-close" @click="closeModal">
                            –ó–∞–∫—Ä—ã—Ç—å
                        </button>
                        <form
                            @submit.prevent="submit"
                            action=""
                            method="POST"
                        >
                            <label>
                                <input type="text" placeholder="E-mail" class="form-input input" v-model="postBody.email">
                            </label>

                            <div class="warning">–í–≤–µ–¥–∏—Ç–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π email</div>


                            <label>
                                <textarea v-model="postBody.textarea" maxlength="10" name="" id="" cols="30" rows="10" placeholder="–û–ø–∏—Å–∞–Ω–∏–µ –ø—Ä–æ–±–ª–µ–º—ã" class="form-input form-textarea"></textarea>
                            </label>
                            <button type="submit" class="modal-default-button" v-on:click="checkForm">
                                –û—Ç–ø—Ä–∞–≤–∏—Ç—å
                            </button>
                        </form>

                    </div>
                </div>
            </div>
        </transition>

    </div>
</template>

<style>
.block-tooltip {
    border-radius: 5px;
    background: #0098ef;
    color: #fff;
    padding: 5px 10px;
    text-align: center;
    font-family: sans-serif;
    font-weight: bold;
    cursor: pointer;
    z-index: 1000;
}
</style>


<script>

// document.addEventListener('DOMContentLoaded', function() {
//
// }, false);

let div = null;
// let prompt_button = document.querySelector(".block-tooltip");
let selection = window.getSelection();

import axios from 'axios'

export default {

    name: 'Main',
    data () {
        return {
            selection: window.getSelection(),
            isModalVisible: false,
            postBody: {
                email: '',
                textarea: '',
                coordinates: "",
                time: '',
                text: '',
                resolution: '',
                url: '',
                ip_user: '',
                type_device: '',
                user_agent: '',
            }
        }
    },
    methods: {
        getIp: function () {
            fetch('http://api.ipify.org/?format=json')
                .then(results => results.json())
                .then(
                    response => {
                        this.some(response)
                    }
                );
        },
        some: function (data) {
            console.log(data.ip);
            this.postBody.ip_user = data.ip;
            return data.ip;
        },
        drawBorderAroundSelection: function () {

            let selection = window.getSelection(), // get the selection then
                range = selection.getRangeAt(0), // the range at first selection group
                rect = range.getBoundingClientRect(); // and convert this to useful data

            if (selection != '') {
                if (div) {
                    div.parentNode.removeChild(div);
                }
                div = document.createElement('div'); // make box
                div.class = 'rect';
                // div.style.border = '2px solid black'; // with outline
                div.innerHTML = "–°–æ–æ–±—â–∏—Ç—å –æ–± –æ—à–∏–±–∫–µ"
                div.style.position = 'fixed'; // fixed positioning = easy mode
                div.classList.add('block-tooltip')
                div.style.top = rect.top - 35 + 'px'; // set coordinates
                div.style.left = rect.left + 'px';
                // div.style.height = 20 + 'px'; // and size
                // div.style.width = 250 + 'px';
                document.body.appendChild(div); // finally append
            } else {
                div.style.display = "none";
            }
        },
        showModal: function (event) {
            if (event.target.className == "block-tooltip") {
                /////////////// get time
                function getTime() {
                    let today = new Date();
                    let date = today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate();
                    let time = today.getHours() + ":" + today.getMinutes() + ":" +
                        today.getSeconds();
                    let dateTime = date+' '+time;
                    return dateTime;
                };
                console.log(getTime());
                this.postBody.time = getTime()

                /////////////////get resolution
                function getScreenResolution() {
                    let height = window.screen.availHeight;
                    let width = window.screen.availWidth;
                    return `${height}px : ${width}px`
                };
                console.log(getScreenResolution());
                this.postBody.resolution = getScreenResolution();

                ///////////////get user agent
                function getUserAgent() {
                    let ua = window.navigator.userAgent;
                    return ua
                };
                console.log(getUserAgent());
                this.postBody.user_agent = getUserAgent();

                //////////////get url
                function getUrl() {
                    let url = window.location.href;
                    return url
                };
                console.log(getUrl());
                this.postBody.url = getUrl();

                /////////////get ip


                this.getIp()
                // this.postBody.ip_user = getIp();
                // let response = fetch('http://api.ipify.org/?format=json');
                // console.log(response)
                // let json =  response.json();
                //
                //
                // if (response.ok) { // –µ—Å–ª–∏ HTTP-—Å—Ç–∞—Ç—É—Å –≤ –¥–∏–∞–ø–∞–∑–æ–Ω–µ 200-299
                //                    // –ø–æ–ª—É—á–∞–µ–º —Ç–µ–ª–æ –æ—Ç–≤–µ—Ç–∞ (—Å–º. –ø—Ä–æ —ç—Ç–æ—Ç –º–µ—Ç–æ–¥ –Ω–∏–∂–µ)
                //
                // } else {
                //     alert("–û—à–∏–±–∫–∞ HTTP: " + response.status);
                // }


                //////////get type device
                function getDeviceType() {
                    const ua = navigator.userAgent;
                    if (/(tablet|ipad|playbook|silk)|(android(?!.*mobi))/i.test(ua)) {
                        return "tablet";
                    }
                    if (
                        /Mobile|iP(hone|od)|Android|BlackBerry|IEMobile|Kindle|Silk-Accelerated|(hpw|web)OS|Opera M(obi|ini)/.test(
                            ua
                        )
                    ) {
                        return "mobile";
                    }
                    return "desktop";
                }
                console.log(getDeviceType());
                this.postBody.type_device = getDeviceType();

            }
        },
        leaveSelection: function (event) {
            if (event.target.className == "block-tooltip") {
                ///////////// get coordinates
                function get–°oordinates() {
                    let range = selection.getRangeAt(0);
                    let rect = range.getBoundingClientRect();
                    let x = rect.top;
                    let y = rect.left;
                    return `x:${x} : y:${y}`
                };
                console.log(get–°oordinates());
                this.postBody.coordinates = get–°oordinates();

                /////////// get text
                function getText() {
                    return selection.toString();
                }
                console.log(getText());
                this.postBody.text = getText();
            }
        },
        closeModal: function () {
            let mask = document.querySelector('.modal-mask');
            mask.style.display = "none"
        },
        openModal: function (event) {
            if (event.target.className == "block-tooltip") {
                let mask = document.querySelector('.modal-mask');
                mask.style.display = "flex"
            }
        },
        checkForm: function() {
            var reg = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
            var address = document.querySelector('.input').value;
            var warning = document.querySelector('.warning')
            if(reg.test(address) == false) {
                warning.style.display = "block"
            } else {
                warning.style.display = "none"
                const str = JSON.stringify(this.postBody);
                let config = {

                }
                axios.post('http://led.43rm.ru/test_rest.php', str, config)
                    .then((response) => {
                        console.log(response);
                    })
                    .catch((error) => {
                        console.log(error.response);
                    });
            }

        },
        submit() {

        }
    },
    created() {
        document.addEventListener("mouseup", this.drawBorderAroundSelection);
        document.addEventListener("click", this.showModal);
        document.addEventListener('mousedown', this.leaveSelection);
        document.addEventListener("click", this.openModal)
    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
    font-weight: normal;
}
ul {
    list-style-type: none;
    padding: 0;
}
li {
    display: inline-block;
    margin: 0 10px;
}
a {
    color: #42b983;
}

</style>
<style>
.warning {
    text-align: center;
    font-size: 14px;
    color: red;
    margin-bottom: 10px;
    display: none;
}
.form-input {
    border-radius: none;
    border: 2px solid #00ec9b;
}
.form-input {
    margin-bottom: 15px;
    width: 100%;
    padding: 5px;
}
.modal-container {
    text-align: right;
}
.button-close {
    margin-bottom: 15px;
    cursor: pointer;
    font-size: 0;
    position: relative;
    width: 20px;
    height: 20px;
}
.button-close::before {
    content: '';
    position: absolute;
    width: 10px;
    height: 1px;
    top: 7px;
    left: 3px;
    background: #000;
    transform: rotate(45deg);
}
.button-close::after {
    content: '';
    position: absolute;
    width: 10px;
    height: 1px;
    top: 7px;
    left: 3px;
    background: #000;
    transform: rotate(135deg);
}
.modal-mask {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: table;
    transition: opacity 0.3s ease;
    display: none;
    justify-content: center;
    align-items: center;
}
.modal-default-button {
    width: 100%;
    background:  #00ec9b;
    color: #fff;
    padding: 10px;
    border: none;
    cursor: pointer;
    font-weight: bold;

}
.modal-wrapper {
    display: table-cell;
    vertical-align: middle;
}

.modal-container {
    width: 300px;
    margin: 0px auto;
    padding: 20px 30px;
    background-color: #fff;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
    transition: all 0.3s ease;
    font-family: Helvetica, Arial, sans-serif;
}

.modal-header h3 {
    margin-top: 0;
    color: #42b983;
}

.modal-body {
    margin: 20px 0;
}

.modal-enter {
    opacity: 0;
}
.modal-leave-active {
    opacity: 0;
}
.modal-enter .modal-container,
.modal-leave-active .modal-container {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}
.form-textarea {
    resize: vertical;
}
</style>




