import Vue from 'vue'
import Router from 'vue-router'
import VTooltip from 'v-tooltip'
import HelloWorld from '@/components/Main'

Vue.use(VTooltip)
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    }
  ]
})



